<template>
  <div class="ebook-reader">
    <div id="read"></div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import Epub from "epubjs";
global.epub = Epub;
export default {
  name: "EbookReader",
  computed: {
    ...mapGetters(["filename"]),
  },
  methods: {
    prevPage() {
      if (this.rendition) {
        this.rendition.prev();
      }
    },
    nextPage() {
      if (this.rendition) {
        this.rendition.next();
      }
    },
    toggleTitleAndMenu() {},
    initEpub() {
      // epuburl
      let url = "http://10.77.162.36:8081/epub/" + this.filename + ".epub";
      this.book = new Epub(url);
      console.log(this.book);
      //   epub
      this.rendition = this.book.renderTo("read", {
        flow: "paginated",
        manager: "continuous",
        width: innerWidth,
        height: innerHeight,
        snap: true
      });
      this.rendition.display();

      // 
      //   this.rendition.on("touchstart", (event) => {
      //     this.touchStartX = event.changeTouches[0].clientX;
      //     this.touchStartTime = event.timeStamp;
      //   });
      //   this.rendition.on("touchend", (event) => {
      //     const offsetX = event.changeTouches[0].clientX - this.touchStartX;
      //     const time = event.timeStamp - this.touchStartTime;
      //     if (time < 500 && offsetX > 40) {
      //       this.prevPage();
      //     } else if (time < 500 && offsetX < -40) {
      //       this.nextPage();
      //     } else {
      //       this.toggleTitleAndMenu();
      //     }
      //     event.preventDefault();
      //     event.stopPropagation();
      //   });
    },
  },
  mounted() {
    this.$store
      .dispatch("setFileName", this.$route.params.filename.split("|").join("/"))
      .then(() => {
        this.initEpub();
      });
  },
};
</script>

